import React from 'react';
import s from "./../../Todolist.module.css";

import {useAppSelector} from "../../redux/redux-store";
import {Task} from "./Task";
import {TaskStatuses} from "../../api/ todolist-api";
import {RequestStatusType} from "../../reducers/appReducer";
import {ItemType} from "../../reducers/tasksReducer";
import {Paper} from "@mui/material";

type TasksMapPropsType = {
    filter:string
    todolistID: string//       

}

export const TasksMap = React.memo(({ todolistID,filter}: TasksMapPropsType) => {
    debugger
    const tasks =useAppSelector<Array<ItemType>>(state => state.tasks[todolistID]); //filter  -   1   
    const status=useAppSelector<RequestStatusType>(state=>state.app.status);
    let tasksFilter = tasks;

    if (filter === 'Active') {

        tasksFilter = tasks.filter(f => f.status===TaskStatuses.New); //             
    }
    if (filter === 'Completed') { // 

        tasksFilter = tasks.filter(f => f.status===TaskStatuses.Completed); //true ,
    }

    return (
        //MaterialUI    React.memo -  useCallback/ 
        <ul className={s.todolist_tasks}>
            {tasksFilter?.map ( el => {// ?          
                return  <Paper className={s.li} elevation={2} key={el.id}>
                    {/*//   'loading'*/}
                <Task taskID={el.id} todolistID={todolistID} disabledStatus={status}/>
               </Paper>})}

        </ul>
    );
});

